<script>
import { Tab, Tabs } from 'vant'
export default {
  name: 'BasketballTeam',
  props: {
    statisticsData: {
      type: Object,
      default: () => { }
    }
  },
  components: {
    VanTab: Tab,
    VanTabs: Tabs
  },
  watch: {
  },
  data () {
    return {
      tabActive: 0,
      domain: 'https://cdn.sportnanoapi.com/basketball/player/',
      hteamPlayers: [],
      ateamPlayers: []
    }
  },
  created () {
    // const lanqiuData = require('@/utils/lanqiuApi.json')
    const lanqiuData = this.statisticsData
    const bTeamData = lanqiuData.players
    this.hteamPlayers = bTeamData[0]
    this.ateamPlayers = bTeamData[1]
  },
  methods: {
    parseTeam (item) {
      const Data = JSON.parse(JSON.stringify(item[6])).split('^')
      const first = Data[16] === '0' ? '' : ''
      const time = Data[0]
      const score = Data[13]
      const shoot = Data[1]
      const three = Data[2]
      return `<span class="player">${item[1]}</span>
        <span class="first">${first}</span>
        <span class="time">${time}</span>
        <span class="score">${score}</span>
        <span class="shoot">${shoot}</span>
        <span class="three">${three}</span>`
    }
  }
}
</script>

